@import url(https://fonts.googleapis.com/css?family=Cormorant+SC|Libre+Baskerville|Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: black;
  overflow-x: hidden;
}

.App {
  position: relative;
  color: white;
  padding: 0 120px;
}

.bg {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url(/got-web-page/static/media/got-dragon.8fe8dd26.jpg) no-repeat center center fixed;
  background-size: cover;
  z-index: -1;
}

.mainHeader {
  margin-top: 120px;
}

.mainHeader h1 {
  font-size: 100px;
  font-family: 'Cormorant SC', serif;
  font-weight: 300;
  margin: 0;
}

.mainBottomPart {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 100px;
}

.popularList p h3 {
  margin: 10px 0;
}

.popularList p {
  opacity: .7;
  font-size: 13px;
}

.storyArticle {
  width: 90%;
  max-width: 400px;
  font-weight: bold;
}

.storyArticle p {
  font-weight: 100;
  color: rgba(255, 255, 255, 0.849);
}

@media only screen and (max-width: 700px) {
  .App {
    padding: 0 30px 30px 30px;
  }
}
.underlineParagraph {
    position: relative;
    cursor: pointer;
    display: table;
}

.underlineParagraph:before {
    content: " ";
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 0%;
    height: 3px;
    transition: width 250ms;
    background-color: rgb(87, 173, 244);
}

.underlineParagraph:hover:before {
    width: 100%;
}   

.navBar {
    position: relative;
    padding: 30px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
  }
  
  .navBar img {
    width: 100px;
    cursor: pointer;
  }
  
  .navMenu {
    display: flex;
    flex-direction: row;
  }
  
  .navMenu * {
    margin-left: 30px;
    cursor: pointer;
  }
  
  .navMenu img {
    width: 20px;
  }

  @media only screen and (max-width: 600px) {
    body {
      background-color: lightblue;
    }
  }
body {
    background-color: black;
}

h1 {
  font-family: 'Cormorant SC', serif;
  font-size: 80px;
  font-weight: 300;
  margin-bottom: 0;
}

.bg {
    width: 100vw;
    height: 100vh;
    background-size: cover;
}

.characterHeader span {
    text-decoration: underline;
    cursor: pointer;
}

.characterHeader p {
    font-family: 'Cormorant SC', serif;
    font-size: 1.5em;
}

.characterInfo {
    width: 90%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.characterInfo > ul{
    list-style-type: none;
}

.characterInfo li {
    margin-top: 10px;
    color: rgba(255, 255, 255, 0.8);
    list-style-type: none;
    font-weight: 100;
}

.characterInfo span {
    font-size: 25px;
    font-weight: bold;
    font-family: 'Cormorant SC', serif;
    color: white;
    cursor: auto;
}

.characterVideo {
    position: fixed;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: black;
    overflow: hidden;
}

.video {
    width: 100%;
    height: 100%;
}

.show {
    display: all;
}

.hide {
    display: none;
}

.videoBack {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(27, 27, 27);
    z-index: 200; 
}

.allCharactersBg {
    background: url(/got-web-page/static/media/got-characters.d7f22e40.jpg) no-repeat center center fixed;
    background-size: cover;
}

.carousel {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}

.characterItem {
    position: absolute;
    width: 400px;
    max-width: 400px;
    top: 120%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 0;
}

.characterItem h2 {
    font-family: 'Cormorant SC', serif;
}

.characterItem p {
    font-size: 13px;
    opacity: .8;
}

.arrowRotate {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.characterItemImage {
    width: 15vw;
    height: 15vw;
    border-radius: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1;
}

.characterImgButtons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: -1;
}

.characterImgButtons img {
    padding: 30px;
}

.aboutBg {
    background: url(/got-web-page/static/media/white-walker.e53ddf8f.jpg) no-repeat center center fixed;
    background-size: cover;
}
.placeBg {
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
}

.placeHeader {
    position: relative;
    display: flex;
    justify-items: center;
    justify-content: center;
    align-content: center;
    align-items: center;
    margin-top: 25vh;
}

.placeHeader h1 {
    margin: 0;
    padding: 0;
}

.placeHeader img {
    padding: 20px;
    cursor: pointer;
}

.placeHeader img:nth-child(1){
    margin-right: 50px;
}

.placeHeader img:nth-child(3){
    margin-left: 50px;
}

.blockContainer {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    display: flex;
    pointer-events: none;
}

.blockContainer > div {
    width: calc(100% / 5);
}

.rotatetArrow {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
